<script setup lang="ts">
import SidebarNav from '@/apps/cp/components/SidebarNav.vue';
import { useRouter } from 'vue-router';

defineProps<{
	menu: {
		icon: String;
		text: String;
		url: String;
		highlight: Boolean;
		children: Object;
	};
}>();

function subIsActive(urls) {
	const currentRoute = useRouter().currentRoute.value.path;
	return urls.some((item) => item.url === currentRoute);
}

function isExternalUrl(url: string) {
	return url.startsWith('http');
}
</script>

<template>
	<!-- menu with submenu -->
	<div v-if="menu.children" class="menu-item has-sub" :class="{ active: subIsActive(menu.children) }">
		<a class="menu-link">
			<span class="menu-icon" v-if="menu.icon">
				<i :class="menu.icon"></i>
				<span class="w-5px h-5px rounded-3 bg-theme position-absolute top-0 end-0 mt-3px me-3px"
					v-if="menu.highlight"></span>
			</span>
			<span class="menu-text">{{ menu.text }}</span>
			<span class="menu-caret" v-if="menu.children"><b class="caret"></b></span>
		</a>
		<div class="menu-submenu">
			<template v-for="(submenu, index) in menu.children" :key="index">
				<SidebarNav :menu="submenu"></SidebarNav>
			</template>
		</div>
	</div>

	<!-- menu without submenu and external link -->
	<a v-else-if="isExternalUrl(menu.url)" :href="menu.url" class="text-decoration-none">
		<div class="menu-item" :class="{ active: isActive }">
			<a :href="href" @click="navigate" class="menu-link">
				<span class="menu-icon" v-if="menu.icon">
					<i :class="menu.icon"></i>
					<span class="menu-icon-label" v-if="menu.label">{{ menu.label }}</span>
				</span>
				<span class="menu-text">{{ menu.text }}</span>
			</a>
		</div>
	</a>

	<!-- menu without submenu and internal link -->
	<router-link v-else :to="menu.url" custom v-slot="{ navigate, href, isActive }">
		<div class="menu-item" :class="{ active: isActive }">
			<a :href="href" @click="navigate" class="menu-link">
				<span class="menu-icon" v-if="menu.icon">
					<i :class="menu.icon"></i>
					<span class="menu-icon-label" v-if="menu.label">{{ menu.label }}</span>
				</span>
				<span class="menu-text">{{ menu.text }}</span>
			</a>
		</div>
	</router-link>
</template>
