<script setup lang="ts">
import { slideToggle } from '@/shared/composables/slideToggle.js';
import { useAppOptionStore } from '@/apps/cp/stores/app-option';
import { RouterLink } from 'vue-router';

const appOption = useAppOptionStore();
const notificationData = [{
	icon: 'fa fa-receipt fa-lg fa-fw text-success',
	title: 'Your store has a new order for 2 items totaling $1,299.00',
	time: 'just now'
}, {
	icon: 'far fa-user-circle fa-lg fa-fw text-body text-opacity-25',
	title: '3 new customer account is created',
	time: '2 minutes ago'
}, {
	img: '/assets/img/icon/android.svg',
	title: 'Your android application has been approved',
	time: '5 minutes ago'
}, {
	img: '/assets/img/icon/paypal.svg',
	title: 'Paypal payment method has been enabled for your store',
	time: '10 minutes ago'
}];

function toggleAppSidebarMinify() {
	if (!(appOption.appTopNav && appOption.appSidebarHide)) {
		appOption.appSidebarMinified = !appOption.appSidebarMinified;
	}
}
function toggleAppSidebarMobileToggled() {
	if (appOption.appTopNav && appOption.appSidebarHide) {
		slideToggle(document.querySelector('.app-top-nav'));
		window.scrollTo(0, 0);
	} else {
		appOption.appSidebarMobileToggled = !appOption.appSidebarMobileToggled;
	}
}
function toggleAppHeaderSearch(event) {
	event.preventDefault();

	appOption.appHeaderSearchToggled = !appOption.appHeaderSearchToggled;
}
function checkForm(event) {
	event.preventDefault();
	this.$router.push({ path: '/extra/search' })
}
function toggleFullscreen() {
	var isFullscreen = document.fullscreenElement || document.mozFullScreenElement || document.webkitFullscreenElement || document.msFullscreenElement;
	var element = document.documentElement;

	if (!isFullscreen) {
		if (element.requestFullscreen) {
			element.requestFullscreen();
		} else if (element.mozRequestFullScreen) {
			element.mozRequestFullScreen();
		} else if (element.webkitRequestFullscreen) {
			element.webkitRequestFullscreen();
		} else if (element.msRequestFullscreen) {
			element.msRequestFullscreen();
		}
	} else {
		if (document.exitFullscreen) {
			document.exitFullscreen();
		} else if (document.mozCancelFullScreen) {
			document.mozCancelFullScreen();
		} else if (document.webkitExitFullscreen) {
			document.webkitExitFullscreen();
		} else if (document.msExitFullscreen) {
			document.msExitFullscreen();
		}
	}
}

function toggleFullscreenIcon() {
	var isFullscreen = document.fullscreenElement || document.mozFullScreenElement || document.webkitFullscreenElement || document.msFullscreenElement;
	const icon = document.getElementById('fullScreenIcon');

	if (!isFullscreen) {
		icon.className = "fa-solid fa-expand nav-icon";
	} else {
		icon.className = "fa-solid fa-compress nav-icon";
	}
}

document.addEventListener('fullscreenchange', toggleFullscreenIcon);
document.addEventListener('mozfullscreenchange', toggleFullscreenIcon);
document.addEventListener('webkitfullscreenchange', toggleFullscreenIcon);
document.addEventListener('msfullscreenchange', toggleFullscreenIcon);

function isMobileDevice() {
	if (/Android|iPhone|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
		return true;
	}
	return false;
}
</script>
<template>
	<div id="header" class="app-header">
		<!-- BEGIN mobile-toggler -->
		<div class="mobile-toggler">
			<button type="button" class="menu-toggler" v-on:click="toggleAppSidebarMobileToggled">
				<span class="bar"></span>
				<span class="bar"></span>
			</button>
		</div>
		<!-- END mobile-toggler -->

		<!-- BEGIN brand -->
		<div class="brand">
			<div class="desktop-toggler">
				<button type="button" class="menu-toggler" v-on:click="toggleAppSidebarMinify">
					<span class="bar"></span>
					<span class="bar"></span>
				</button>
			</div>

			<RouterLink class="container brand-bg" to="/">
				<img src="/assets/img/brand/logo-wide.svg" alt="ArgusGuard Logo">
			</RouterLink>
		</div>
		<!-- END brand -->

		<!-- BEGIN menu -->
		<div class="menu">
			<div class="menu-item" v-if="!isMobileDevice()">
				<a class="menu-link fullscreen-toggler" href="#" aria-expanded="false" @click="toggleFullscreen">
					<div class="menu-icon">
						<i class="fa-solid fa-expand nav-icon" id="fullScreenIcon"></i>
					</div>
				</a>
			</div>
			<div class="menu-item dropdown">
				<a href="#" data-bs-toggle="dropdown" data-display="static" class="menu-link">
					<div class="menu-icon"><i class="fa fa-bell nav-icon"></i></div>
					<div class="menu-label">{{ notificationData.length }}</div>
				</a>
				<div class="dropdown-menu dropdown-menu-end dropdown-notification">
					<h6 class="dropdown-header text-body-emphasis mb-1">Benachrichtigungen</h6>
					<template v-if="notificationData && notificationData.length > 0">
						<a href="#" class="dropdown-notification-item" v-for="(notification, index) in notificationData"
							v-bind:key="index">
							<div class="dropdown-notification-icon">
								<i v-if="notification.icon" v-bind:class="notification.icon"></i>
								<img v-if="notification.img" v-bind:src="notification.img" width="26">
							</div>
							<div class="dropdown-notification-info">
								<div class="title">{{ notification.title }}</div>
								<div class="time">{{ notification.time }}</div>
							</div>
							<div class="dropdown-notification-arrow">
								<i class="fa fa-chevron-right"></i>
							</div>
						</a>
					</template>
					<template v-else>
						<div class="dropdown-notification-item">
							Du hast keine neuen Benachrichtigungen.
						</div>
					</template>
					<div class="p-2 text-center mb-n1">
						<RouterLink to="/account/notifications"
							class="text-body-emphasis text-opacity-50 text-decoration-none">
							Benachrichtigungen
							verwalten</RouterLink>
					</div>
				</div>
			</div>
			<div class="menu-item dropdown">
				<a href="#" data-bs-toggle="dropdown" data-display="static" class="menu-link">
					<div class="menu-img online">
						<img src="/assets/img/user/profile.jpg" alt="" class="ms-100 mh-100 rounded-circle">
					</div>
					<div class="menu-text">Max Mustermann</div>
				</a>
				<div class="dropdown-menu dropdown-menu-end me-lg-3">
					<RouterLink to="/account/settings" class="dropdown-item d-flex align-items-center">Einstellungen <i
							class="fa fa-cog fa-fw ms-auto text-gray-400 fs-16px"></i></RouterLink>
					<div class="dropdown-divider"></div>
					<a href="https://auth.argus-guard.com/logout"
						class="dropdown-item d-flex align-items-center">Abmelden <i
							class="fa fa-toggle-off fa-fw ms-auto text-gray-400 fs-16px"></i></a>
				</div>
			</div>
		</div>
		<!-- END menu -->
	</div>
</template>
