import { createRouter, createWebHistory } from "vue-router";
import Cookies from "js-cookie";
import i18n from '@/apps/cp/main';

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes: [

    // DASHBOARD
    { path: '/', meta: { title: 'Übersicht' }, component: () => import('@/apps/cp/views/Dashboard.vue') },

    // SERVICE
    { path: '/server/:id', meta: { title: 'Serverübersicht' }, component: () => import('@/apps/cp/views/service/Dashboard.vue') },
    { path: '/server/:id/databases', meta: { title: 'Datenbanken' }, component: () => import('@/apps/cp/views/service/Databases.vue') },
    { path: '/server/:id/filemanager', meta: { title: 'Dateimanager' }, component: () => import('@/apps/cp/views/service/FileManager.vue') },
    { path: '/server/:id/backups', meta: { title: 'Backups' }, component: () => import('@/apps/cp/views/service/Backups.vue') },
    { path: '/server/:id/tasks', meta: { title: 'Aufgaben' }, component: () => import('@/apps/cp/views/service/Tasks.vue') },
    { path: '/server/:id/network', meta: { title: 'Netzwerk' }, component: () => import('@/apps/cp/views/service/Network.vue') },
    { path: '/server/:id/settings', meta: { title: 'Einstellungen' }, component: () => import('@/apps/cp/views/service/Settings.vue') },

    // ACCOUNT
    { path: '/account/finances/balance', meta: { title: 'Guthaben' }, component: () => import('@/apps/cp/views/account/finances/Balance.vue') },
    { path: '/account/finances/transactions', meta: { title: 'Transaktionen' }, component: () => import('@/apps/cp/views/account/finances/Transactions.vue') },
    { path: '/account/finances/payment', meta: { title: 'Zahlungsmethoden' }, component: () => import('@/apps/cp/views/account/finances/Payment.vue') },
    { path: '/account/notifications', meta: { title: 'Benachrichtigungen' }, component: () => import('@/apps/cp/views/account/Notifications.vue') },
    { path: '/account/settings', meta: { title: 'Einstellungen' }, component: () => import('@/apps/cp/views/account/Settings.vue') },

    // UTILS
    { path: '/choose-lang', meta: { title: 'Language' }, component: () => import('@/apps/cp/views/utils/ChooseLang.vue') },
    { path: '/coming-soon', meta: { title: 'Coming Soon' }, component: () => import('@/apps/cp/views/utils/ComingSoon.vue') },
    { path: '/maintenance', meta: { title: 'Maintenance' }, component: () => import('@/apps/cp/views/utils/Maintenance.vue') },

    // 404
    { path: '/:pathMatch(.*)*', meta: { title: 'Error 404' }, component: () => import('@/apps/cp/views/utils/PageError.vue') },

  ],
  scrollBehavior(to, from, savedPosition) {
		if (savedPosition) {
		  return savedPosition;
		}
		return { top: 0 };
	  },
});

router.beforeEach((to, from, next) => {
  // Inject meta information
  const title: string = (to.meta.title as string) || 'ComingSoon';
  const description: string = (to.meta.description as string) || 'ComingSoon';
  const visible: boolean = to.meta.visible == false;

  document.title = title + ' | Key2Host';

  const descriptionElement = document.querySelector('head meta[name="description"]');
  if (descriptionElement) {
    descriptionElement.setAttribute('content', description);
  }

  let robotsElement = document.querySelector('head meta[name="robots"]');
  if (!robotsElement) {
    robotsElement = document.createElement('meta');
    robotsElement.setAttribute('name', 'robots');
    document.head.appendChild(robotsElement);
  }
  robotsElement.setAttribute('content', visible ? 'index, follow' : 'noindex, nofollow');

  // Language check and other logic...
	const langC = Cookies.get('lang');
	if (!langC && to.path !== '/choose-lang') {
		const browserLang = navigator.language;
		const supportedLangs = ['de', 'en'];
		const detectedLang = supportedLangs.find(lang => browserLang.startsWith(lang));

		if (detectedLang) {
			i18n.global.locale.value = detectedLang as 'de' | 'en';
		} else {
			next('/choose-lang');
		}
	} else {
		if (langC) {
			i18n.global.locale.value = langC as 'de' | 'en';
		}
	}

  // Authentication check
  const isLoggedIn = Cookies.get('id_token');
  if (to.path !== '/choose-lang' && !isLoggedIn) {
    // window.location.href = 'https://auth.unisuite.de/';
  }

  next();
});

export default router;
