<script setup lang="ts">
import { ref, onMounted, getCurrentInstance } from 'vue';
import { RouterView } from 'vue-router';
import { useAppOptionStore } from '@/apps/cp/stores/app-option';
import { useAppVariableStore, generateVariables } from '@/shared/stores/app-variable';
import { ProgressFinisher, useProgress } from '@marcoschulte/vue3-progress';
import { Toast } from 'bootstrap';
import AppSidebar from '@/apps/cp/components/Sidebar.vue';
import AppHeader from '@/apps/cp/components/Header.vue';
import AppFooter from '@/apps/cp/components/Footer.vue';
import AppTopNav from '@/apps/cp/components/TopNav.vue';
import router from '@/apps/cp/router';

const appVariable = useAppVariableStore();
const appOption = useAppOptionStore();
const internalInstance = getCurrentInstance();
const progresses = [] as ProgressFinisher[];
var isLoading = ref(false);
var isInactive = ref(false);
var inactiveTimeout;
var loadingDots = ref(".");

onMounted(() => {
	let dotCount = 1;
	setInterval(() => {
		if (dotCount > 3) dotCount = 1;
		loadingDots.value = ".".repeat(dotCount);
		dotCount++;
	}, 750);
});


router.beforeEach(async (to, from) => {
	isLoading.value = true;
	progresses.push(useProgress().start());
	appOption.appSidebarMobileToggled = false;

	var targetElm = [].slice.call(document.querySelectorAll('.app-sidebar .menu-submenu'));
	targetElm.map(function (elm) {
		elm.style.display = '';
	});
})
router.afterEach(async (to, from) => {
	isLoading.value = false;
	document.body.scrollTop = 0;
	document.documentElement.scrollTop = 0;
	progresses.pop()?.finish();
})

const onlineStatusChanged = (event) => {
	if (event.type === 'offline') {
		new Toast(document.getElementById('iOffline')).show();
	} else if (event.type === 'online') {
		new Toast(document.getElementById('iOnline')).show();
	}
};

document.addEventListener("mousemove", () => {
	isInactive.value = false;
	clearTimeout(inactiveTimeout);
	inactiveTimeout = setTimeout(() => {
		isInactive.value = true;
	}, 2 * 60 * 1000); // 2 minuten
});

function changeColorSheme(newColorScheme) {
	appOption.appMode = newColorScheme;
	document.documentElement.setAttribute('data-bs-theme', newColorScheme);
	var newVariables = generateVariables();
	appVariable.font = newVariables.font;
	appVariable.color = newVariables.color;
}

window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', event => {
	const newColorScheme = event.matches ? "dark" : "light";
	changeColorSheme(newColorScheme);
});

if(window.matchMedia('(prefers-color-scheme: dark)').matches) {
	changeColorSheme("dark");
}

window.addEventListener('offline', onlineStatusChanged);
window.addEventListener('online', onlineStatusChanged);
document.querySelector('body').classList.add('app-init');
</script>

<template>
	<div class="app" v-bind:class="{
		'app-header-menu-search-toggled': appOption.appHeaderSearchToggled,
		'app-sidebar-minified': appOption.appSidebarMinified,
		'app-sidebar-collapsed': appOption.appSidebarCollapsed,
		'app-sidebar-mobile-toggled': appOption.appSidebarMobileToggled,
		'app-sidebar-mobile-closed': appOption.appSidebarMobileClosed,
		'app-content-full-height': appOption.appContentFullHeight,
		'app-content-full-width': appOption.appSidebarHide,
		'app-with-top-nav': appOption.appTopNav,
		'app-without-sidebar': appOption.appSidebarHide,
		'app-without-header': appOption.appHeaderHide,
		'app-boxed-layout': appOption.appBoxedLayout,
		'app-footer-fixed': appOption.appFooterFixed,
	}">
		<vue3-progress-bar />
		<div v-if="isLoading" class="d-flex justify-content-center align-items-center">
			<div class="position-fixed top-0 start-0 w-100 h-100 bg-black opacity-90 d-flex flex-column align-items-center justify-content-center"
				style="z-index: 9999;">
				<div class="spinner-border mb-2 text-white" style="width: 3rem; height: 3rem;" role="status">
					<span class="visually-hidden">Loading...</span>
				</div>
				<span class="text-white">{{ $t("GENERAL.WAIT") + loadingDots }}</span>
			</div>
		</div>
		<div v-if="isInactive && !isLoading" class="d-flex justify-content-center align-items-center">
			<div class="position-fixed top-0 start-0 w-100 h-100 bg-black opacity-90 d-flex flex-column align-items-center justify-content-center"
				style="z-index: 9999;">
				<div class="mb-2">
					<i class="fa-solid fa-moon fa-2xl fa-beat-fade text-white" style="--fa-animation-duration: 4s;"></i>
				</div>
				<h2>Fenster inaktiv</h2>
				<span class="text-white text-center">Du hast seit einiger Zeit nicht mehr mit der WebApp
					interagiert.</span>
				<span class="text-white text-center">Bewege deine Maus um fortzufahren.</span>
			</div>
		</div>
		<app-header v-if="!appOption.appHeaderHide" />
		<app-sidebar v-if="!appOption.appSidebarHide" />
		<AppTopNav v-if="appOption.appTopNav" />
		<div class="app-content" v-if="!isInactive" v-bind:class="appOption.appContentClass">
			<router-view></router-view>
		</div>
		<app-footer v-if="appOption.appFooter" />
		<div class="toasts-container">
			<div class="toast" data-autohide="false" id="iOffline">
				<div class="toast-header">
					<i class="far fa-bell text-muted me-2"></i>
					<strong class="me-auto">Wichtige Meldung</strong>
					<small>Jetzt</small>
				</div>
				<div class="toast-body">
					Die Verbindung zum Server ist gestört.
				</div>
			</div>
			<div class="toast" data-autohide="false" id="iOnline">
				<div class="toast-header">
					<i class="far fa-bell text-muted me-2"></i>
					<strong class="me-auto">Wichtige Meldung</strong>
					<small>Jetzt</small>
				</div>
				<div class="toast-body">
					Die Verbindung zum Server wurde wiederhergestellt.
				</div>
			</div>
		</div>
	</div>
</template>
